import React from 'react';
import PropTypes from 'prop-types';
import Styles from './Button.module.scss';
import classnames from 'classnames';

/** `Button`       .  */
const Button = ({
    children,
    theme,
    size,
    disabled,
    iconOnly,
    onClick,
    ...props
  }) => {
    return (
      <button
        className={classnames(
          theme === 'primary' ? Styles.Primary :
          theme === 'secondary' ? Styles.Secondary :
          theme === 'tertiary' ? Styles.Tertiary : Styles.Primary,
          iconOnly ? ( size === 'small' ? Styles.IconSmall : 
                       size === 'medium' ? Styles.IconMedium : 
                       size === 'big' ? Styles.IconBig : Styles.IconMedium)
          : ( size === 'small' ? Styles.Small : 
              size === 'medium' ? Styles.Medium : 
              size === 'big' ? Styles.Big : Styles.Medium ),
          iconOnly ? Styles.IconOnly : ""
        )}
        disabled={disabled}
        style={props.style}
        onClick={onClick}
      >
        {children}
      </button>
      // <StyledButton
      //   themes={theme}
      //   sizes={size}
      //   iconOnly={iconOnly}
      //   iconOnlySizes={iconOnly ? size : "default"}
      //   disabled={disabled}
      //   width={width}
      //   onClick={onClick}
      // >
          
      // </StyledButton>
    );
  };

Button.propTypes = {
  /**    */
  children: PropTypes.node,
  /**     */
  onClick: PropTypes.func,
  /**   . */
  theme: PropTypes.oneOf(['primary', 'secondary', 'tertiary']),
  /**    */
  size: PropTypes.oneOf(['small', 'medium', 'big']),
  /**   . */
  disabled : PropTypes.bool,
  /**    . */
  width: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number
  ]),
  /**       `true` . */
  iconOnly : PropTypes.bool,
};
  
Button.defaultProps = {
  theme: 'primary',
  size: 'medium',
  disabled: false,
  iconOnly: false
};

export default Button;